html{
  height: 100%;
  margin: 0px;
  /*background: -webkit-linear-gradient(rgba(51, 204, 204, 0.3), rgb(255, 255,255));
  */
  background-image: -webkit-linear-gradient(rgb(19 203 221 / 30%), rgb(3 161 159)), url(back.jpg);
  background-size: 600px;
  font-size: 16px;
  transition-property: background-size;
  transition-duration: 2s;
  background-repeat: repeat;
  background-color: gray;
  background-attachment: fixed;
  animation-name: background-animation;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
}

/*html:hover{
  background-size: 720px;
}*/

@keyframes background-animation{
  from{
     background-size: 720px;
  }
  to{
     background-size: 600px;
  }
}
@keyframes photo-animation{
  from{
     opacity: 0.0;
     transform: translateY(-10px) scale(1.2);
  }
  to{
     opacity: 0.9;
     transform: translateY(0px) scale(1);
  }
}

.a-link-address{
  align-self: center;
}
.project-site{
    width: 150px;
    margin: auto;
    padding: 10px;
    background: #33CCCC;
    border-radius: 10px;
    border: 1px solid gray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 10px;
    right: 0;
    left: 0;
}
.project-site a{
  text-decoration: none;
  text-transform: uppercase;
}

.top-title-resume{
  width: 400px;
  height: 63px;
  background: #009999;
  display: flex;
  justify-content: center;
  justify-items: center;
  border-radius: 50px 50px 0px 0px;
  margin: auto;
  margin-top: 10px;
}

.top-title-resume p{
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: uppercase;
  text-shadow: 1px 2px 2px #33CCCC;
  font-family: monospace;
}

.base-info{
  width: 370px;
  height: 150px;
  /*background: #33CCCC;*/
  background: rgba(0,153,153,.5);
  margin: auto;
  margin-top: 14px;
  border-radius: 10px;
  position: relative;
}

#m-photo{
  width: 100px;
  height: 110%;
  position: absolute;
  top: -5%;
  left: 1%;
  border-radius: 10px;
  opacity: 0.9;
  text-align: center;
  transition: all;
  transition-duration: 1s;
  cursor: pointer;
  animation-name: photo-animation;
  animation-duration: 3s;
  background-color: gray;
  background-size: cover;
}
#m-photo:hover{
  transform: translateY(-10px) scale(1.2);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 1.0);
}

.base-info-label-div h3{
  text-align: center;
  text-shadow: 1px 2px 2px rgba(51,204,204,.8);
  font-size: 1.3rem;
}

.base-info-label-div{
  width: 60%;
  height: 30%;
  background: #009999;
  border-radius: 10px 10px 10px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 2px;
  right: 2px;
  font-family: ExoBold;
  letter-spacing: -1px;
  word-spacing: 2px;
  border: 2px solid rgba(0,228,288,.5);
}

.base-info-description-div{
  width: 60%;
  height: 70%;
  position: absolute;
  bottom: 3px;
  right: 3px;  
  padding-top: 10px;
}

.base-info-description-div span{
  color: #BBFFFF;
  font-size: 1rem;
  font-family: Phill;
  letter-spacing: -1px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  background: rgba(0, 153, 153, 0.5);
  margin-right: 10px;
  font-weight: bold;
  letter-spacing: 1px;
  border-radius: 5px 5px 5px 0px;
}

.base-info-description-div p{
  font-size: 1.3rem;
  line-height: 25%;
  font-family: Phill;
}
span, h1, h2, h3, p{
  color: #FFFFFF;
}


.out-test{
  width: 250px;
  height: 300px;
  background: #009999;
  border-radius: 20px 20px 0px 20px;
  position: relative;
  border: 5px double #0eadad;
  margin-left: 20px;
  margin-bottom: 20px;
}
.inner-test{
  width: 99%;
  height: 99%;
  background: #008484;
  position: absolute;
  top: 5px;
  left: 5px;
  border-radius: 5% 20px 10px 20px;  
  border: 3px solid #018d8d;
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
}
.inner-test h2{
  text-align: center;
  text-shadow: 1px 2px 2px #33CCCC;
  width: 100%;
  letter-spacing: -1px;
  font-family: ExoBold;
  font-size: 1.5rem;
}
.inner-test h2::first-letter{
  color: #64ffff;
  margin-right: 2px;
}
.inner-test p{
  text-align: center;
  word-spacing: 1px;
  font-family: Phill;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 3px;
  font-size: 1rem;
}

.div-work-experience{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

#my-stack-div{
  width: 400px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  padding-top: 20px;
  margin: auto;
}

#my-stack-div .out{
  background: #33CCCC;
  border-radius: 5px;
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  border: 1px solid #019179;
}

#my-stack-div .in{
  background: #009999;
  border-radius: 5px 5px 2px 5px;
  padding-left: 15px;
   padding-right: 15px;
   position: relative;
   top: 3px;
   left: 2px;
   display: inline-block;
}

#my-stack-div h2{
  font-size: 0.9rem;
  font-family: sans-serif;
  font-weight: normal;
  letter-spacing: 1px;
  color: #FFFFFF;
  text-shadow: 1px 2px 2px #33CCCC;
}

#div-contact{
  position: absolute;
  top: 40px;
  left: 20px;
  transition: all .5s;
}


.container-contact{
  width: min-content;
  height: 40px;
  position: relative;
  margin-bottom: 30px;
  cursor: pointer;
}

.contact-text{
  width: 100%;
  height: 100%;
  background-color: #0086c8;
  opacity: 1;
  border-radius: 10px 10px 10px 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, .5);
  overflow: hidden;
  transition: all .5s;
  padding-left: 40px;
}
.contact-text p{
  font-size: 1.1rem;
  font-family: serif;
  color: white;
  text-align: center;
  line-height: 30%;
}
.div-contact-icon{
  width: 40px;
  height: 40px;
  border-radius: 20px;
  box-shadow: inset 0px 0px 2px #356e8a, inset -1px -1px 5px #345e8a, 0px 0px 15px #0088cc;
  background-size: contain;
  position: absolute;
  top: 0;
  left: -5px;
  cursor: pointer;
  transition: transform, width;
  transition-duration: 1s;
}
.div-contact-icon:hover{
  box-shadow: inset 1px 1px 5px #356e8a, inset -1px -1px 5px #345e8a, 0px 0px 12px #0088cc;
  transform: rotate(360deg);
  width: 45px;
  height: 45px;
}


.div-download{
  width: 180px;
  height: 40px;
  position: relative;
  margin-bottom: 30px;
}

.download-text{
  width: 100%;
  height: 100%;
  background: #009999;
  display: inline-block;
  border-radius: 20px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, .5);
  padding-left: 15px;
  overflow: hidden;
  transition: all .5s;
}

.download-text p{
  line-height: 50%;
  font-family: sans-serif;
  color: #FFFFFF;
  display: inline-block;
  text-align: center;
  width: 100%;
  white-space: nowrap;
  font-size: 1rem;
}

.div-photo{
  width: 40px;
  height: 40px;
  background-image: url(pdf.png);
  background-size: cover;
  border: 1px solid gray;
  border-radius: 50%;
  border: 2px dashed #33CCCC;
  position: absolute;
  top: 0;
  left: -5px;
}



.testComponent{
  width: 100px;
  height: 100px;
  background-color: black;
  position: absolute;
  top: 0px;
  right: 0px;
  margin: 0;
  padding: 0;
}

.flag-selector{
  position: absolute;
  top: 0px;
  right: 0px;
  display: block;
}

.flag{
  width: 40px;
  height: 40px;
  background-size: contain;
}

.top-bar-selector{
  width: 80%;
  height: 50px;
  background-color: #009999;
  border-radius: 0px 0px 30px 30px;
  display: none;
  opacity: .8;
  justify-items: center;
  padding-right: 10px;
  padding-left: 10px;
  box-shadow: 1px 1px 15px black;
  position: relative;
  margin: auto;
  margin-bottom: 50px;
}


.flag-app-bar{
    width: 30px;
    height: 30px;
    background-size: contain;
    padding-right: 0px;
}

.language-selector-app-bar{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
}


.contact-app-bar{
  width: 30px;
  height: 30px;
  background-size: contain;
  border-radius: 50%;
  box-shadow: 1px 1px 15px black;
  align-self: center;
  margin-left: 10px;
}


@media screen and (max-width: 950px){
  .contact-text{
     width: 0%;
  }
  .download-text{
     width: 10%;
  }
}
@media screen and (max-width: 580px){
  html{
    margin:1%;
    font-size: 15px;
  }
  #div-contact{
    display: none;
  }
  .top-bar-selector{
    display: flex;
  }
  .flag-selector{
    display: none;
  }
  .base-info{
    width: 330px;
  }
  .top-title-resume{
    width: 340px;
  }
}






























